import React from "react";
import DealsWrapper from "./DealsWrapper";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";

async function Deals({ fetchData, title }) {
  let products = [];
  let error = "";
  try {
    products = await fetchData();
  } catch (err) {
    error = err;
  }
  products = products.data;

  return (
    <section className="mt-5 flex justify-center w-full sm:w-3/4 md:w-2/3 lg:w-1/2 xl:w-2/3 bg-muted py-10 rounded-lg shadow-lg">
      <div className="w-full max-w-screen-md px-6 py-4">
        <h1 className="text-center text-3xl font-bold text-primary">
          {title}
        </h1>
        <ScrollArea className="mt-6 max-h-96 overflow-y-auto px-2">
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            {products?.length > 0 ? (
              products.slice(0, 6).map((product, index) => (
                <DealsWrapper key={index} product={product}>
                  <Card className="relative overflow-hidden group">
                    {product.discountPercentage && (
                      <Badge className="absolute top-2 right-2 bg-destructive text-white">
                        {product.discountPercentage}% OFF
                      </Badge>
                    )}
                    <CardHeader>
                      <img
                        className="h-40 w-full object-cover rounded-md transition-transform duration-300 group-hover:scale-105"
                        src={product.image}
                        alt={product.name}
                      />
                    </CardHeader>
                    <CardContent className="flex flex-col items-center text-center space-y-2">
                      <CardTitle className="text-lg font-semibold">
                        {product.name}
                      </CardTitle>
                      <p className="text-primary font-semibold">
                        ${product.offerPrice}
                        <span className="text-sm line-through text-muted-foreground ml-2">
                          ${product.actualPrice}
                        </span>
                      </p>
                      <Button variant="outline">Buy Now</Button>
                    </CardContent>
                  </Card>
                </DealsWrapper>
              ))
            ) : (
              <p className="p-4 text-muted-foreground text-center">No deals available</p>
            )}
          </div>
        </ScrollArea>
      </div>
    </section>
  );
}

export default Deals;
